.c_link_button {
  text-decoration: none;
  outline: none;
  display: inline-block;
  font-size: 20px;
  font-weight: 800;
}

.c_link_hover_style_button_container {
  position: relative;
  overflow: hidden;
  transition: color 300ms linear, background 300ms linear;
  padding: 14px 18px;
}
.c_link_hover_style_button_container .link_hover_style_button_label {
  position: relative;
  z-index: 10;
}
.c_link_hover_style_button_container::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: -100%;
  transition: transform 300ms linear;
}
.c_link_hover_style_button_container::before {
  background-color: #fafafa;
}
.darkmode {
  background-color: #1e2022;
}

.c_link_hover_style_button_container:hover::before {
  z-index: 5;
  transform: translate(100%, 0);
}