.onboarding_section {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 100px;
  height: calc(100vh - 58px);
  background-color: #1e2022;
}
.onboarding_section {
  color: #fafafa;
}
.darkmode {
  color: #1e2022;
}

.onboarding_section .onboarding_page_intro_section {
  display: block;
  font-size: 20px;
  animation: smooth_opacity_hidden 2000ms ease-in;
  opacity: 0;
}
.onboarding_section .onboarding_page_name_section {
  display: flex;
}
.onboarding_section.hidden_trans {
  opacity: 0;
  transition: opacity 500ms linear 1500ms;
}

@keyframes smooth_opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes smooth_opacity_hidden {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.onboarding_page_second_word {
  display: inline-block;
  animation: smooth_opacity 2000ms ease-in;
  font-weight: 800;
  user-select: none;
}