@font-face {
  font-family: "Noto Sans KR", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap") format("woff2");
  unicode-range: U+AC00-D7A3, U+0030-0039, U+0020-002F, U+003A-0040, U+005B-0060, U+007B-007E;
}
@font-face {
  font-family: "Noto Sans", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap") format("woff2");
  unicode-range: U+0041-005A, U+0061-007A;
}
* {
  box-sizing: border-box;
  vertical-align: top;
  margin: 0;
  padding: 0;
  border: 0;
  background-color: transparent;
  line-height: 1.6;
}

body {
  font-family: "Noto Sans KR", "Noto Sans", sans-serif;
}
body {
  background-color: #fafafa;
}
.darkmode {
  background-color: #1e2022;
}